
.paragraph--type--list {
  .paragraph--type--list-item {
    padding-top: 15px;
    padding-bottom: 15px;
    padding-left: 20px;
  }
}

.sfgov-drupal-two-columns-list {
  .field__items {
    @include media($medium-screen) {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
    }
  }
}

.path-frontpage {
  .field--type-link.__link {
    a:visited {
      color: $color-white;
    }
  }
}

.parent--bundle--page {
  .sfgov-container {
    padding: 0;
  }
  h3 {
    margin-top: 0;
    color: $color-blue-darker;
  }
  .__text {
    margin-bottom: 30px;
    h3 {
      @include source-sans-pro;
      color: $color-gray-dark;
      font-size: 20px;
      line-height: 26px;
      @include media($medium-screen) {
        margin-bottom: 18px;
      }
    }
    p {
      color: $color-gray;
      margin: 10px 0;
    }
  }
  &.paragraph--type--in-person-location .eck-entity {
    .__title {
      @include source-sans-pro;
      color: $color-gray-dark;
      font-size: 20px;
      line-height: 26px;
      font-weight: bold;
      margin-bottom: 8px;
    }
    .__address {
      margin-bottom: 8px;
    }
    .__operating-hours {
      color: $color-gray;
    }
  }
}

.path-frontpage {
  .sfgov-services {
    .sfgov-cta-button__container {
      a {
        margin-left: 15px;
      }
    }
  }
}

.paragraph--type--cost {
  .__cost-flat-fee, .__text {
      color: $color-gray;
  }
}

.paragraph--type--thing-to-know p {
  color: $color-gray;
}

.sfgov-people {
  margin: 0 20px;
  > * {
    border-bottom: 1px solid $color-gray-light;
    &:last-of-type {
      border-bottom: none;
    }
  }
}

.path-frontpage .block-views-exposed-filter-blocksearch-page-1 {
  position: relative;
}

#sfgov-top-search-suggestions-container {
  clear: both;
  position: absolute;
  background: $color-white;
  width: 100%;
  padding: 0;
  box-shadow: #aaa 0px 5px 5px 2px;
  border-width: 0 1px 1px 1px;
  border-color: #999;
  border-style: solid;
  > h4 {
    margin: 10px 25px 0 25px;
    padding: 0;
    font-family: inherit;
    font-size: 1.7rem;
  }
}

.sfgov-top-search-suggestion {
  display: none;
  float: none !important;
  a {
    text-decoration: none;
    display: block;
    padding: 10px 25px;
    color: $color-gray;
    // @include open-sans;
    &:hover {
      background-color: $color-aqua-lightest;
    }
  }
}

.path-frontpage #views-exposed-form-search-page-1 .js-form-item-keyword {
  width: 100%;
}

.path-frontpage #views-exposed-form-search-page-1 #edit-submit-search {
  width: 100%;
}